@font-face {
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-family: "Panton";
  src: url("../fonts/panton-regular.woff2") format("woff2"), url("../fonts/panton-regular.woff") format("woff");
}
@font-face {
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  font-family: "Panton";
  src: url("../fonts/panton-bold.woff2") format("woff2"), url("../fonts/panton-bold.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  font-family: "Panton";
  src: url("../fonts/panton-semibold.woff2") format("woff2"), url("../fonts/panton-semibold.woff") format("woff");
}
@font-face {
  font-weight: 800;
  font-style: normal;
  font-display: swap;
  font-family: "Panton";
  src: url("../fonts/panton-extrabold.woff2") format("woff2"), url("../fonts/panton-extrabold.woff") format("woff");
}
@font-face {
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  font-family: "Panton";
  src: url("../fonts/panton-black.woff2") format("woff2"), url("../fonts/panton-black.woff") format("woff");
}
.header {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  outline: 1px solid red;
}
.header__high {
  height: 45px;
  display: flex;
  color: #ffffff;
  background-color: #1d1e1e;
  opacity: 0.5;
  outline: 2px solid red;
}
.header__high-container {
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
  outline: 2px solid orange;
}
.header__high-call {
  font-size: 13px;
  line-height: 25px;
}
.header__high-container button {
  font-weight: 600;
  font-size: 20px;
  color: #009ee2;
  color: green;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.header__middle {
  width: 1200px;
  min-height: 124px;
  margin: 0 auto;
  padding: 0 30px 0 30px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  outline: 1px solid green;
}
.header__middle-logotype {
  margin-right: 26px;
}
.header__middle-form input {
  width: 554px;
  height: 40px;
  margin-right: 41px;
  padding-left: 23px;
  box-sizing: border-box;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  font-family: "Panton", "Arial", sans-serif;
  word-spacing: 3.5px;
  border: 1px solid orange;
  border-radius: 4px;
  background-color: transparent;
}
.header__middle-functional {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 247px;
  height: 45px;
  display: flex;
  outline: 1px solid red;
}
.header__low {
  min-height: 60px;
  outline: 1px solid tomato;
}
.header__low-container {
  display: flex;
  justify-content: space-between;
  width: 1200px;
  margin: 0 auto;
  padding: 0 30px 0 30px;
  box-sizing: border-box;
  outline: 1px solid lightgreen;
}
.header__low-catalog {
  width: 263px;
  height: 60px;
  font-size: 18px;
  line-height: 25px;
}
.navigation-dark {
  outline: 1px solid red;
}
.navigation-dark__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  outline: 1px solid orange;
}
.navigation-dark__item a {
  font-size: 14px;
  line-height: 25px;
  color: #ffffff;
}
.logotype {
  font-weight: 900;
  font-size: 50px;
  line-height: 20px;
  letter-spacing: 4.5px;
  color: #0086c0;
  outline: 1px solid red;
}
.navigation-light {
  list-style: none;
  margin: 0;
  padding: 0;
  flex-grow: 1;
  display: flex;
  outline: 1px solid green;
}
.navigation-light__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  outline: 1px solid red;
}
.navigation-light__item {
  outline: 1px solid red;
  display: flex;
  align-items: center;
}
.navigation-light__item a {
  font-weight: 600;
  font-size: 14px;
  line-height: 25px;
  text-transform: uppercase;
  outline: 1px solid green;
}
.button {
  font-family: "Panton", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  background-color: #29afe2;
  background-color: transparent;
  border: none;
  border-radius: 4px;
}
.index-main {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.index-main-first-wrapper {
  display: flex;
  justify-content: space-between;
  min-height: 740px;
  width: 1200px;
  margin: 0 auto;
  padding: 30px 30px 100px 30px;
  box-sizing: border-box;
  border-bottom: 1px solid #efefef;
  border-bottom: 1px solid green;
}
.goods {
  width: 262px;
  min-height: 610px;
  border-radius: 4px;
  border: 1px solid red;
}
.goods-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.goods-item {
  border-bottom: 1px solid green;
}
.goods-item:last-child {
  border-bottom: none;
}
.goods-item a {
  display: block;
  padding: 13px 0 13px 62px;
  box-sizing: border-box;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  font-family: "Panton", "Arial", sans-serif;
}
.slide {
  width: 846px;
  height: 610px;
  outline: 1px solid red;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.sales-hits {
  width: 1200px;
  min-height: 674px;
  margin: 0 auto;
  padding: 0 30px 0 30px;
  box-sizing: border-box;
  border-bottom: 1px solid red;
}
.sales-hits__wrapper {
  display: flex;
  align-items: flex-end;
  min-height: 124px;
  padding-bottom: 49px;
  box-sizing: border-box;
  outline: 1px solid red;
}
.sales-hits h2 {
  padding: 0;
  margin: 0;
  margin-right: 32px;
  font-weight: 800;
  font-size: 36px;
  line-height: 25px;
  letter-spacing: 1.6px;
  text-transform: uppercase;
  color: green;
}
.sales-hits__catalog {
  font-weight: 800;
  font-size: 11px;
  line-height: 11px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-decoration: underline;
  color: #009ee2;
  color: tomato;
}
.sales-hits__switches {
  margin-left: auto;
  outline: 1px solid red;
}
