body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-family: "Panton", "Arial", sans-serif;
  text-transform: none;
  background-image: url("../img/substrate/main.jpg");
  background-position: top center;
  background-repeat: no-repeat;
}
a {
  color: #2A2939;
  text-decoration: none;
}
